
#head {
    background-color: #f2f2f2;
    padding: 20px;
}

.app-nav {
    display: flex;
    padding: 10px;
    background-color: black;
}

a {
    text-decoration: none;
    color: skyblue;
    margin-right: 15px;
}

.table {
	display: block;
	margin-top: 35px;
}

.userTable {
    display: flex;
}

.tableField {
    background-color: skyblue;
    padding: 5px;
    margin: 2px;
    min-width: 250px;
    text-align: left;
    display: inline-block;
    overflow: hidden;
}

span {
    font-size: 180%;
    padding: 0;
    margin: 0;
    position: absolute;
    right: 14px;
    top: 0;
  }

  main {
	margin-left: auto;
	margin-right: auto;
	display: block;
	width: 70%;
}

button {
    margin: 10px 9px 0px 0px;
  }